*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
body{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background: #f1f5f8;
    color:#222;
}
.booklist{
    width:90vw;
    margin:5rem auto;
    display: grid;
    gap: 2rem;
    max-width: 1170px;
}
.book{
    position: relative;
    background: #fff;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
}
.book img{
    width: 100%;
    object-fit: cover;

}
.book h2{
    margin-top: 1rem;
    font-size: 1rem;
}
.number{
    position: absolute;
    top: 0;
    left: 0;
    font-size: 1rem;
    padding: 0.75rem;
    background: #c35600;
    border-top-left-radius:1rem ;
    border-bottom-right-radius:1rem ;
    color: #fff;
}
h1{
    text-align: center;
    margin-top: 4rem;
    text-transform: capitalize;
}
@media(min-width:768px){
    .booklist {
        grid-template-columns: repeat(3,1fr);
    }
}
